@import "resets.scss";

* {
    box-sizing: border-box; }
body {
    overflow-x: hidden; }

.container {
    max-width: 1100px;
    position: relative;
    margin: 0 auto; }

.overlay {
    min-height: 800px;
    width: 100vw;
    position: absolute;
    background: rgba(0, 0, 0, 0.3); }

// навигационное меню
.nav-menu {
    width: 500px;
    &__list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 0; }
    &__list-item {
        a {
            text-decoration: none;
            color: white; } } }

// главная секция
.main-section {
    min-height: 800px;
    position: relative;
    background-image: url("../img/картинка\ фона\ 1\ секция\ .jpg");
    z-index: 0;
    background-size: cover;
    background-position: center;

    &__container-nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        z-index: 10;
        margin-bottom: 60px; }
    &__block {
        margin: 0 auto;
        z-index: 10; } }

// блок по середине главной секции
.block {
    max-width: 930px;
    min-height: 500px;
    display: flex;
    background: rgba(217, 217, 217, 0.43);
    backdrop-filter: blur(4px);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    border-radius: 20px;

    &__right-wrapper {
        width: 45%;
        background: rgba(27, 17, 219, 0.42);
        border-radius: 20px;
        padding: 100px 0px 0px 40px;
        display: flex;
        flex-direction: column; }
    &__btn {
        margin-bottom: 30px; }
    &__left-wrapper {
        width: 55%;
        padding: 40px 0px 0px 60px;
        h2 {
            font-size: 32px;
            color: white; } }
    &__text {
        font-size: 48px;
        color: white;
        width: 300px;
        margin-bottom: 40px; }
    &__title {
        text-align: center; } }


// иконки под кнопкой
.icons {
    &__item {
        height: 55px;
        margin-right: 30px;
        &:last-child {
            margin: 0; } } }

// кнопка
.button {
    background-color: #3D3A3A;
    border-radius: 20px;
    width: 13em;
    height: 4em;
    padding-bottom: 6px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    &__text {
        font-size: 24px;
        text-align: center;
        color: white;
        margin: 0; } }

// заголовок с линией

.title {
    font-size: 64px;
    position: relative;
    font-weight: bold;
    &__hr {
        width: 90px;
        position: absolute;
        bottom: 0px;
        margin: 0;
        border: none;
        border-top: 2px solid black;
        opacity: 1;
        &_white {
            border-color: white; } }
    &_white {
        color: white; } }

.second-section {
    min-height: 900px;
    background:  rgba(27, 17, 219, 0.42);
    width: 100vw;
    &__icons-container {
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px; }
    &__img {
        height: 120px;
        margin-bottom: 20px; }
    &__text-container {
        display: flex;
        justify-content: space-between; }
    &__text {
        max-width: 150px;
        text-align: center;
        margin-bottom: 0;
        font-size: 20px;
        span {
            font-size: 25px; } } }




// карусель

.carousel-inner {
    margin-bottom: 2rem; }
.carousel-item {
    position: relative;
    img {
        height: 550px;
        object-fit: cover; } }

// теуст внутри карусели
.content {
    max-width: 700px;
    min-height: 300px;
    position: absolute;
    top: 20%;
    left: 20%;
    background: rgba(217, 217, 217, 0.1);
    backdrop-filter: blur(4px);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    border-radius: 40px;
    padding: 10px 10px 10px 30px;
    &__title {
        color: white;
        font-size: 50px;
        margin-bottom: 20px; }
    &__text {
        color: white;
        font-size: 32px;
        margin-bottom: 0; } }

// третья секция
.third-section {
    min-height: 550px;
    background: #d9d9d9;
    padding-top: 40px;


    &__content-container {
        display: flex; }
    &__left-wrapper {
        max-width: 600px;
        margin-right: 100px; }
    &__right-wrapper {
        display: flex;
        align-items: center;
        img {
            height: 350px; } } }
// точка и текст
.textpoint {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 40px;
    &__point {
        height: 15px;
        width: 15px;
        border-radius: 100%;
        background-color: black;
        margin-right: 20px; }
    &__text {
        max-width: 500px;
        margin-bottom: 0; } }


// четвертая секция
.fourth-section {
    min-height: 800px;
    background: rgba(27, 17, 219, 0.21);
    padding-top: 40px;
    &__left-wrapper {
       max-width: 600px;
       margin-right: 90px; }
    &__title {
        margin-bottom: 80px;
        line-height: 0.95; }
    &__content-container {
        display: flex; }
    &__text-title {
        font-size: 40px;
        margin-bottom: 60px; }


    &__right-wrapper {
        max-width: 500px;
        min-height: 500px;
        backdrop-filter: blur(4px);
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        background: rgba(27, 17, 219, 0.25);
        border-radius: 30px;
        padding: 20px 20px;
        display: flex;
        flex-direction: column-reverse;
        justify-content: space-between; }
    &__img {
        max-width: 500px;
        min-height: 300px;
        border-radius: 30px;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25); } }


.accordion-body {
    font-size: 20px; }



// ффутер
.footer-section {
    min-height: 700px;
    background: url("../img/изображение\ Юфу\ хакатон.webp") no-repeat;
    background-size: cover;
    background-position: center;
    &__overlay {
        min-height: 700px;
        background:  rgba(4, 0, 16, 0.87);
        width: 100vw;
        position: absolute; }
    &__text {
        color: white;
        margin-bottom: 0;
        font-size: 36px; }
    &__menu {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 50px;
        margin-bottom: 50px; }
    &__contacts {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 40px; }

    &__numbers {
        display: flex;
        flex-direction: column;
        p {
            color: white;
            font-size: 32px;
            &:last-child {
                margin-bottom: 0; } } }
    &__map {
        width: 1000px;
        margin: 0 auto; }
    iframe {
        width: 100%; } }

