                margin-bottom: 10px; } } }







    .nav-menu {
        transition: 1s all;
        position: fixed;
        top: 0;
        right: -100%;
        height: 100vh;
        width: 50%;
        z-index: 10;
        background-color: grey;
        &__list {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            height: 100%; }
        &__list-item {
            margin-right: 0; } }



    .hamburger {
        width: 30px;
        height: 14px;
